/* eslint-disable no-console */

import { register } from "register-service-worker";

if (process.env.NODE_ENV === "production") {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready() {
      console.log(
        "La aplicacin est siendo servida desde la cach por un trabajador del servicio\n" +
          "Para obtener ms detalles, visite https://goo.gl/AFskqB"
      );
    },
    registered() {
      console.log("El trabajador del servicio ha sido registrado.");
    },
    cached() {
      console.log("El contenido se ha almacenado en cach para su uso sin conexin.");
    },
    updatefound() {
      console.log("Se est descargando contenido nuevo.");
    },
    updated() {
      console.log("Hay nuevo contenido disponible; porfavor refresca.");
    },
    offline() {
      console.log(
        "No se encontr conexin a Internet. La aplicacin se est ejecutando en modo fuera de lnea."
      );
    },
    error(error) {
      console.error("Error durante el registro del trabajador del servicio:", error);
    },
  });
}
